import { getCommentInfo } from '@/api/user'
export default {
  name: 'uc-commentsDetail',
  data () {
    return {
      commentInfo: {
      }
    }
  },
  computed: {
  },
  created () {
    const postData = {
      id: this.$route.query.id
    }
    getCommentInfo(postData)
      .then(res => {
        if (res.data.errorcode === 0) {
          // 处理数据
          this.commentInfo = res.data.data
          console.log(res)
        } else {
          this.$vux.toast.text(res.data.errormsg, 'middle')
        }
      })
      .catch(err => {
        console.log(err)
        this.$vux.toast.text('网络异常！', 'middle')
      })
  },
  components: {

  },
  methods: {
    test () {
      console.log(2323)
    }
  }
}



